@import url(https://fonts.googleapis.com/css2?family=Merriweather:ital@1&family=Slabo+27px&display=swap);
* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  height: 100%;
  background-image: url(/weather-app-react.git/static/media/clouds.57510ee4.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
  z-index: -2;
  opacity: 1;
}

/*
#background img {
   opacity: 0.5;
} */

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* MainContent - Apps */
.main-content {
   position: absolute;
   top: 8%;
   left: 50%;
   transform: translateX(-50%);
   width: 55vw;
   height: auto;
   display: flex;
   flex-direction: row;
   flex-wrap: wrap;
   justify-content: center;
}

.app-container {
   display: block;
   align-items: center;
   justify-content: center;
   flex-wrap: wrap;
}

#app-name {
   text-decoration: underline;
   display: inline-block;
   position: relative;
   top: 10px;
   left: 50%;
   transform: translateX(-50%);
   font-size: 20px;
   z-index: -1;
}

.app {
   width: 200px;
   height: 210px;
   margin: 20px 25px 40px 25px;
   border: 2.9px solid #09000c;
   border-radius: 8px;
   cursor: pointer;
   background-color: rgb(196, 223, 230, 0.3);
}

.app > img {
   width: 140px;
   height: 140px;
   margin: 50%;
   transform: translate(-50%, -50%);
   /* display: block;
   align-items: center;
   justify-content: center; */
}


/* Weather App */
.weather {
   position: absolute;
   top: -3%;
   left: 50%;
   transform: translateX(-50%);
   width: 90vw;
   min-height: 650px;
   border: 0.5px solid white;
   border-radius: 20px;
   box-shadow: 3px 3px 15px 6px rgba(190,217,247,0.5);
   display: flex;
   flex-direction: column;
   align-items: center;
   background-color: rgb(16, 42, 73, 0.985);
   cursor: auto;
   -webkit-animation: appear 0.4s ease-out forwards;
           animation: appear 0.4s ease-out forwards;
}

@-webkit-keyframes appear {
   0% {
      opacity: 0.4;
      top: -5%;
   }
   50% {
      opacity: 0.7;
   }
   100% {
      opacity: 1;
      top: -3%;
   }
}

@keyframes appear {
   0% {
      opacity: 0.4;
      top: -5%;
   }
   50% {
      opacity: 0.7;
   }
   100% {
      opacity: 1;
      top: -3%;
   }
}

#cityName {
   margin-top: 20px;
   margin-bottom: 25px;
   font-size: 33px;
   letter-spacing: 3px;
   text-decoration: underline;
   color: white;
   font-family: 'Merriweather', serif;
   z-index: 1;
}

.weather-container {
   display: flex;
   flex-direction: row;
   flex-wrap: wrap;
   align-items: center;
   justify-content: center;
}

.day-box {
   width: 220px;
   height: 260px;
   margin: 20px 20px 20px 30px;
   border: 0.7px solid white;
   border-radius: 8px;
   display: flex;
   flex-direction: column;
   align-items: center;
   box-shadow: 3px 5px 5px 5px rgba(227,220,227,1);
   box-shadow: 3px 3px 10px 4px rgba(190,217,247,0.2);
   -webkit-animation: appear-days 0.4s ease-out forwards;
           animation: appear-days 0.4s ease-out forwards;
   color: white;
}

#today {
   width: 240px;
   height: 392px;
}

#today .max-temp {
   right: -55%;
}

@-webkit-keyframes appear-days {
   0% {
      opacity: 0.4;
      margin-top: 35px;
   }
   100% {
      opacity: 1;
      margin-top: 20px;
   }
}

@keyframes appear-days {
   0% {
      opacity: 0.4;
      margin-top: 35px;
   }
   100% {
      opacity: 1;
      margin-top: 20px;
   }
}

#dayName {
   color: black;
   font-size: 27px;
   margin-top: 8px;
   font-family: 'Slabo 27px', serif;
}

.min-max-temp {
   font-size: 20px;
}
.min-temp {
   position: relative;
   left: 13px;
}
.max-temp {
   position: relative;
   right: -52%;
}

#temp {
   font-size: 43px;
   margin-top: 10px;
}

#wicon {
   width: 60px;
   height: 60px;
   margin: 17px 0px 12px;
}

.extras {
   display: flex;
   flex-direction: column;
   align-items: center;
   font-size: 18px;
}

.extras > span, .extras > div > span {
   margin-bottom: 5px;
}



